import React, { useEffect, useState } from 'react';
import {
  View,
  Text,
  Modal,
  StyleSheet,
  TouchableOpacity,
  Image,
  ActivityIndicator,
  Dimensions
} from 'react-native';
import { profileAPI, ProfileData } from '../services/api';
import LetterAvatar from './LetterAvatar';

interface ProfileModalProps {
  visible: boolean;
  username: string;
  onClose: () => void;
}

const ProfileModal: React.FC<ProfileModalProps> = ({ visible, username, onClose }) => {
  const [profile, setProfile] = useState<ProfileData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    if (visible) {
      loadProfile();
    } else {
      setProfile(null);
      setError('');
    }
  }, [visible, username]);

  const loadProfile = async () => {
    try {
      setLoading(true);
      setError('');
      const data = await profileAPI.getProfile(username);
      setProfile(data);
    } catch (error) {
      console.error('  :', error);
      setError('   ');
    } finally {
      setLoading(false);
    }
  };

  const renderStats = () => {
    if (!profile) return null;

    const stats = [
      { label: '', value: profile.posts },
      { label: '', value: profile.subscribers },
      { label: '', value: profile.subscriptions }
    ];

    return (
      <View style={styles.stats}>
        {stats.map((stat) => (
          <View key={stat.label} style={styles.statItem}>
            <Text style={styles.statNumber}>
              {stat.value?.toLocaleString() || '0'}
            </Text>
            <Text style={styles.statLabel}>{stat.label}</Text>
          </View>
        ))}
      </View>
    );
  };

  return (
    <Modal
      visible={visible}
      animationType="slide"
      transparent={true}
      onRequestClose={onClose}
    >
      <View style={styles.modalOverlay}>
        <View style={styles.modalContent}>
          <TouchableOpacity style={styles.closeButton} onPress={onClose}>
            <Text style={styles.closeButtonText}></Text>
          </TouchableOpacity>

          {loading ? (
            <ActivityIndicator size="large" color="#007AFF" style={styles.loader} />
          ) : error ? (
            <Text style={styles.errorText}>{error}</Text>
          ) : profile ? (
            <View style={styles.profileContent}>
              {profile.cover && (
                <Image 
                  source={{ uri: profile.cover }} 
                  style={styles.coverImage}
                />
              )}

              <View style={styles.avatarContainer}>
                {profile.avatar ? (
                  <Image 
                    source={{ uri: profile.avatar }} 
                    style={styles.avatar}
                  />
                ) : (
                  <LetterAvatar 
                    name={profile.name || profile.username} 
                    size={80}
                  />
                )}
              </View>

              <Text style={styles.name}>{profile.name}</Text>
              <Text style={styles.username}>@{profile.username}</Text>
              
              {profile.description && (
                <Text style={styles.description}>{profile.description}</Text>
              )}

              {renderStats()}

              {profile.icons && profile.icons.length > 0 && (
                <View style={styles.iconsContainer}>
                  {profile.icons.map((icon, index) => (
                    <Image
                      key={index}
                      source={{ uri: `https://elemsocial.com/Content/Icons/${icon}` }}
                      style={styles.icon}
                    />
                  ))}
                </View>
              )}
            </View>
          ) : null}
        </View>
      </View>
    </Modal>
  );
};

const styles = StyleSheet.create({
  modalOverlay: {
    flex: 1,
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    justifyContent: 'center',
    alignItems: 'center',
  },
  modalContent: {
    backgroundColor: 'white',
    borderRadius: 15,
    width: '90%',
    maxHeight: '80%',
    overflow: 'hidden',
  },
  closeButton: {
    position: 'absolute',
    right: 15,
    top: 15,
    zIndex: 2,
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    width: 30,
    height: 30,
    borderRadius: 15,
    justifyContent: 'center',
    alignItems: 'center',
  },
  closeButtonText: {
    fontSize: 20,
    color: '#fff',
    fontWeight: 'bold',
  },
  loader: {
    padding: 20,
  },
  profileContent: {
    alignItems: 'center',
  },
  coverImage: {
    width: '100%',
    height: 150,
  },
  avatarContainer: {
    marginTop: -40,
    marginBottom: 10,
    borderRadius: 40,
    borderWidth: 4,
    borderColor: '#fff',
    overflow: 'hidden',
  },
  avatar: {
    width: 80,
    height: 80,
  },
  name: {
    fontSize: 20,
    fontWeight: 'bold',
    marginBottom: 4,
  },
  username: {
    fontSize: 16,
    color: '#666',
    marginBottom: 10,
  },
  description: {
    fontSize: 14,
    color: '#444',
    textAlign: 'center',
    marginHorizontal: 20,
    marginBottom: 15,
  },
  stats: {
    flexDirection: 'row',
    justifyContent: 'space-around',
    width: '100%',
    paddingVertical: 15,
    borderTopWidth: 1,
    borderBottomWidth: 1,
    borderColor: '#eee',
  },
  statItem: {
    alignItems: 'center',
    flex: 1,
  },
  statNumber: {
    fontSize: 18,
    fontWeight: 'bold',
  },
  statLabel: {
    fontSize: 12,
    color: '#666',
    marginTop: 4,
  },
  iconsContainer: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    justifyContent: 'center',
    padding: 10,
    gap: 8,
  },
  icon: {
    width: 24,
    height: 24,
  },
  errorText: {
    color: 'red',
    textAlign: 'center',
    padding: 20,
  },
});

export default ProfileModal; 